.App {
  text-align: center;
  background-image: url("./img/FUNDO.PNG");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.App-logo {
  height: 25vmin;
  pointer-events: none;
  user-select: none; /* Desabilita a seleção de texto */
  -webkit-user-drag: none; /* Desabilita a ação de arrastar */
}

.App-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  color: #5ac65e;
  font-weight: bold;
}

.App-header .left {
  display: flex;
  align-items: center;
  user-select: none; /* Desabilita a seleção de texto */
}

.App-header .left img {
  height: 40px;
  margin-right: 10px;
}

.App-header .right button {
  margin: 0 10px;
  padding: 10px 20px;
  background-color: transparent;
  color: white;
  border: 2px solid #5ac65e;
  cursor: pointer;
  transition: background-color 0.3s; /* Transição suave */
}

.App-header .right button:hover,
.App-header .right button.active {
  background-color: #5ac65e; /* Fundo verde ao passar o mouse ou se ativo */
}

.App-main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1; /* Para ocupar o espaço restante */
}

.content-container {
  display: flex;
  align-items: center;
}

.App-title {
  font-size: 4rem; /* Aumente o tamanho do texto conforme necessário */
  margin-left: 0px;
  color: #5ac65e;
  user-select: none;
  -webkit-user-drag: none;
}

.App-footer {
  color: white;
  padding: 20px 0;
  font-size: 2rem;
  user-select: none;
  -webkit-user-drag: none;
}

.App-section {
  padding: 20px;
  font-size: 1.5rem;
  background-color: rgba(
    40,
    44,
    52,
    0.8
  ); /* Semi-transparente para ver o fundo */
  color: white;
  margin: 20px;
  border-radius: 10px;
}
